	module.exports = {"data":{"post":{"html":"<p>I just rearranged my <a href=\"http://www.papascott.de/referers.php\">referers page</a> to sort chronologically, which seems to make more sense for a site with little traffic like this one.  I'm still using <a href=\"http://ranchero.com/php/rollingreferers/\">Brent's code</a>, but added a MAX(d) column to the SQL query, and sort on it: </p>\n<p><code>SELECT DISTINCT url, COUNT(*) as ct, MAX(d) as maxd from referers GROUP BY url ORDER BY maxd DESC, url;</code></p>\n<p>I could do some more intelligent things with the URLs (like sort out duplicates and highlight the really <a href=\"http://searchrequests.weblogs.com/\">disturbing</a> Google queries), but maybe another day.</p>","fields":{"title":"What Have You Done For Me Lately?","date":"12 Jun 2002","url":"https://papascott.de/2002-06-12-what-have-you-done-for-me-lately/"}}},"pathContext":{"slug":"/2002-06-12-what-have-you-done-for-me-lately/","prev":{"fields":{"title":"He's Back!","date":"12 Jun 2002","slug":"/2002-06-12-hes-back/"}},"next":{"fields":{"title":"World Cup: How German soccer fans feel","date":"12 Jun 2002","slug":"/2002-06-12-world-cup-how-german-soccer-fans-feel/"}}}}

